/* eslint-disable jsx-a11y/alt-text */
import React from 'react'
import '../pagecss/Header.css'
import headerimg from '../imges/imgHeader.jpg'
import { Container, Row } from 'react-bootstrap';

function Header() {
  return (
    <div className='nav-top' >
    <Container>
      <Row>
        <div className='left-head col-md-7 col-lg-7'>
            <h1>Make healthy life
               with <span>fresh grocery</span>
                products</h1>
                <p>Get the best quality and most delicious grocery food in the world,<br /> you can get them our website. Fresh grocery every day to your family.</p>
                <button>Shop Now</button>
        </div>
        <div className=' col-md-5 col-lg-5'>
          <img src={headerimg} />
        </div>
      </Row>
    </Container>
  </div>
  )
}

export default Header
