import React from 'react'
import '../pagecss/order.css'
import shopping from '../imges/deliveryMan.jpg'
import icon4 from '../imges/icon4.png'
import icon5 from '../imges/icon5.png'
import icon6 from '../imges/icon6.png'
import icon7 from '../imges/icon7.png'
function Order() {
  return (
    <div>
        <div className='container order'>
      <div className='row'>
          <div className='col-md-6 col-lg-6 data'>
              <h3>Why We’re Best Quality Grocery Shopper</h3>
              <p>We offer a wide variety of products to choose from you can find everything you need to feed your family.</p>
              <ul>
                <li> <img src={icon4} alt="" /> Extra Fastest Delivery</li>
                <li><img src={icon5} alt="" />Track and Manage Orders</li>
                <li><img src={icon6 } alt="" />Customer Support</li>
                <li><img src={icon7} alt="" />Competitive Price</li>
              </ul>
          </div>
          <div className='col-md-5 col-lg-5 imgorder'>
            <img src={shopping} alt="" />
          </div>
      </div>
      
    </div>
    </div>
  )
}

export default Order
