import React from 'react'
import {Navigation,Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';
import '../pagecss/Shoping.css'
import 'swiper/css/navigation';
import { Swiper, SwiperSlide } from 'swiper/react';

import meet from '../imges/meet.jpg'
import dairy from '../imges/dairy.jpg'
import fruits from '../imges/fruits.jpg'
import vegetables from '../imges/vegetables.jpg'
import herbs from '../imges/herbs.jpg'
function Shoping() {
  return (
    <div className='container'>
        <div className='head'>
        <h1>Popular Categories</h1>
        <a href="/Categories"><button>See All<i class="fa-solid fa-arrow-right"></i></button></a>
      </div>
<Swiper
        slidesPerView={1}
        spaceBetween={10}
        navigation={true} 
        
        modules={[Navigation]}
        breakpoints={{
          640: {
            slidesPerView: 2,
            spaceBetween: 40,
          },
          768: {
            slidesPerView:2 ,
            spaceBetween: 40,
          },
          1024: {
            slidesPerView: 4,
            spaceBetween: 50,
          },
        }}
      
        className="mySwiper"
      >
          <SwiperSlide className="slide1">
          <a href="/">
          <img src={meet} alt="" />
          </a>
        <div>
        <h1>Meet</h1>
        <p>(4item)</p>
        </div>
        </SwiperSlide>
        <SwiperSlide className='slide2'> 
          <a href=""><img src={dairy} alt="" /></a> 
          
          <h1>Dairy</h1>
          <p>(4item)</p>
          </SwiperSlide>
          <SwiperSlide className='slide3'>
          <a href="">  <img src={fruits} alt="" /></a>
          <h1>Fruits</h1>
          <p>(4item)</p>
        </SwiperSlide>
        <SwiperSlide className='slide4'>
          <a href=""><img src={vegetables} alt="" /></a>
          <h2>Vegetables</h2>
          <p>(4item)</p>
          </SwiperSlide>
        <SwiperSlide className='slide5'> 
          <a href=""><img src={herbs} alt="" /></a>
          <h1>Herbs</h1>
          <p>(4item)</p>
          </SwiperSlide>
      </Swiper>
    </div>
  )
}

export default Shoping
