import React from 'react'
import '../pagecss/Applecation.css'
import icon2 from '../imges/icon2.png'
import icon3 from '../imges/icon3.png'
import phone from '../imges/phone.jpg'
function Applecation() {
  return (
    <div>
        <div>
      
      <div className='container '>
        <div className='row'>
          <div className='col-md-6 col-lg-6 aplecation '>
            <h2>Download Our Grocery <br />Mobile App And Save <br /> Time, Money</h2>
              <p>Our grocery mobile app makes it easy to shop for groceries on the go.You can browse our selection of products, create a shopping list, and order groceries for pickup or delivery.</p>
              <img src={icon2} alt=""/>
              <img src={icon3} alt="" />
          </div>
          <div className='col-md-5 col-lg-5 images'>
              <img src= {phone} alt="" />
          </div>
        </div>
      </div>
      </div>
    </div>
  )
}

export default Applecation
