import React from 'react'
import Navebarhome from '../Home/Navebarhome';
import '../ApiProduct/ApiProduct.css'
import { CartProvider, useCart } from "react-use-cart";
function Cart() {
  const {
    isEmpty,
    totalUniqueItems,
    items,
    totalItems,
    cartTotal,
    updateItemQuantity,
    removeItem,
    emptyCart,
  } = useCart();

  if (isEmpty) return <p>Your cart is empty</p>;

  return (
    <>
    <div className="empty d-flex align-items-center justify-content-center">
    <h1 className="text-center">Cart ({totalUniqueItems})</h1>
    <button onClick={()=>emptyCart()}>Empty Cart</button>
    </div>
      <div className="container">
          <div className="row">
            <div className="col-md-10 m-auto" >

          <table className="table table-dark text-center">
            <thead>
              <tr>
                <th>Image</th>
                <th>Name</th>
                <th>Unit</th>
                <th>Price</th>
                <th>Opreaction</th>
              </tr>
            </thead>
            <tbody>

        {items.map((item) => (
          <tr key={item.id}>
            <td>
              <img src={item.img} style={{width:'70px'}}></img>
            </td>
            <td>{item.name}</td>
            <td>{item.quantity}</td>
            <td>{item.price*item.quantity}</td>
            <td>
            <button
              onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)}
            >
              -
            </button>
            <button className="btn-crnter "
              onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)}
            >
              +
            </button>
            <button 
            onClick={() => removeItem(item.id)}>&times;</button>
            </td>
          </tr>
        ))}

        <tr>
          <td>Total</td>
          <td>---------</td>
          <td>{totalItems}</td>
          <td>{cartTotal}</td>
        </tr>
            </tbody>
          </table>
            </div>
          </div>
      </div>
    </>
  );
}
function AllCart() {
  return (
    <CartProvider>
      <Navebarhome/>
      <Cart/>
    </CartProvider>
  )
}

export default AllCart