import React from 'react'
import { useEffect } from 'react';
import logo from '../imges/logo.jpg';
import '../App.css'
import {Navbar,Container,Nav,Button } from 'react-bootstrap';
import'../pagecss/nave.css'
function Navebarhome() {
  useEffect(() => {
    const el = document.querySelector("#headerEl");
    const handleScroll = () => {
      if (window.scrollY > 100) {
        el.classList.add("scroll-down");
      } else {
        el.classList.remove("scroll-down");
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  return (

    <Navbar expand="lg" id="headerEl" className='navebar'>
    <Container>
      <Navbar.Brand href="/">
        <img src={logo} alt="" />
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link href="/">Home</Nav.Link>
          <Nav.Link href="/About">About</Nav.Link>
          <Nav.Link href="/Categories">Categories</Nav.Link>
          <Nav.Link href="/AllProduct">Shopping</Nav.Link>
          <Nav.Link href="/Cart"><i className="fa-solid fa-cart-shopping"></i></Nav.Link>
          <Nav.Link href="/"><Button>Log in</Button></Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
  )
}
export default Navebarhome
