/* eslint-disable jsx-a11y/alt-text */
import { CartProvider, useCart } from "react-use-cart";
import Navebarhome from '../Home/Navebarhome'
import ApiData from './ApiData'
import './ApiProduct.css'
import Footer from "../Home/Footer";
function Page() {
  const { addItem } = useCart();
  return (
       <div className='product'>
        <div className="container">
          <div className="row">
      {ApiData.map((item) =>(item?.items?.map((al)=>(
            <div key={al.id} className='project col-md-3 text-center mb-3'>
             <img src={al.img} alt="" className='img-fluid' style={{height:'200px'}} />
              <h2>{al.name}</h2>
             <div className='linetow'>
                 <div>
                 <p>±1{al.unit}</p>
                 </div>
               <div>
                 <p>${al.price}USD</p>
                </div>
              </div>
             <p><i class="fa-solid fa-star"></i><i class="fa-solid fa-star"></i><i class="fa-solid fa-star"></i><i class="fa-solid fa-star"></i><i class="fa-regular fa-star"></i> ({al.reviewCount}Reviews )</p>
          <button onClick={() => addItem(al)}>Add to cart</button>
        </div>
        ))
      ))}
          </div>
        </div>
    </div>
  );
}



function AllProduct() {
  return (
    <CartProvider>
        <Navebarhome/>
        <br />
        <br />
        <br />
      <Page />
      <Footer/>
    </CartProvider>
  );
}

export default AllProduct